.singleUser {
    display: flex;
    justify-content: space-around;
    align-items: center;
    border: 1px solid black;
}

input {
    margin-right: 10px;
    margin-bottom: 20px;
}

button {
    margin-right: 10px;
    letter-spacing: 2px;
    text-transform: uppercase;
}

