.navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 20px;
    background-color: black;
}

.navbar a {
    text-decoration: underline;
    text-underline-position: under;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: white;
}

.token_no_details {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
}

.name {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    border: 1px solid black;
}

.name p {
    margin-right: 30px;
}
.date_serach{
    display: flex;
    justify-content: center;
    align-items: center;
}